// About Block
.about-block {
  .about-block-content {
    padding-left: 30px;
    padding-right: 30px;
  }
}

// About Block Animations
.about-block .content-block-inner.active {
  @include animation(1s);
  animation-delay: .2s;
  @extend .fade-in-down;
}
.about-content.active {
  .what-we-do-block {
    @include animation(1s);
    animation-delay: .4s;
    @extend .fade-in-left;
  }
}

.about-block .content-block-inner .list {
     list-style-type: none;   
}